import { Contract, ContractInterface } from "@ethersproject/contracts";
import {
  JsonRpcProvider,
  JsonRpcSigner,
  Web3Provider,
} from "@ethersproject/providers";
import { useMemo } from "react";
import { useWallet } from "use-wallet";
import { getProvider } from "../utils/utils";

const contractdescribers = {};

function importAll(r) {
  return r.keys().forEach((key) => {
    const path = key.split("/");
    const last = path[path.length - 1];
    contractdescribers[last.substring(0, last.indexOf("."))] = r(key);
  });
}

importAll(require.context(`../abis/`, true, /\.json$/));

export default function useContract(
  name: string,
  chainId: number,
  contractAddress: string
): Contract {
  const wallet = useWallet();
  const { address, abi } = getContract(name);
  const { ethereum } = wallet;
  if (address) {
    contractAddress = address;
  }
  return useMemo(() => {
    let provider = getProvider(chainId);
    if (ethereum && ethereum.networkVersion === String(chainId)) {
      provider = new Web3Provider(ethereum, "any");
    }
    if (!provider) {
      return null;
    }
    return new Contract(
      contractAddress,
      abi,
      getProviderOrSigner(provider, wallet.account)
    );
  }, [contractAddress, abi, wallet]);
}

// account is not optional
function getSigner(provider: JsonRpcProvider, account: string): JsonRpcSigner {
  return provider.getSigner(account).connectUnchecked();
}

// account is optional
function getProviderOrSigner(
  provider: JsonRpcProvider,
  account?: string
): JsonRpcProvider | JsonRpcSigner {
  return account ? getSigner(provider, account) : provider;
}

function getContract(name: string): ContractDescriber {
  return contractdescribers[name];
}

interface ContractDescriber {
  address: string;
  abi: ContractInterface;
}
